import { auth } from "../config/firebase-config";
import {
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  signOut,
  reload,
} from "firebase/auth";
import { useEffect, useState } from "react";
import {
  Container,
  Card,
  Button,
  Form,
  Alert,
} from "react-bootstrap";

export const Auth = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [msg, setMsg] = useState();
  const [isSignUp, setIsSignUp] = useState(false);

  const signUp = async () => {
    try {
      setMsg("");
      await createUserWithEmailAndPassword(auth, email, password);
      reload(auth);
      window.location.reload();
    } catch (err) {
      console.log(err);
      setMsg(err.message);
    }
  };

  const signIn = async () => {
    try {
      await signInWithEmailAndPassword(auth, email, password);
      reload(auth);
      window.location.reload();
    } catch (err) {
      console.log(err);
      setMsg(err.message);
    }
  };

  const logout = async () => {
    try {
      setMsg("");
      await signOut(auth);
      window.location.reload();
    } catch (err) {
      setMsg(err.message);
    }
  };

  const handleSubmit = async () => {
    isSignUp ? await signUp() : await signIn();
  };

  useEffect(() => {
    console.log("Auth component mounted");
  }, []);

  return (
    <Container
      className="d-flex justify-content-center align-items-center"
      style={{ }}
    >
      <Card className="p-4 shadow-sm w-100" style={{ maxWidth: "400px" }}>
        {auth?.currentUser ? (
          <div className="text-center">
            <h5 className="mb-3">Signed in as:</h5>
            <p><strong>{auth.currentUser.email}</strong></p>
            <Button variant="danger" onClick={logout}>
              Logout
            </Button>
          </div>
        ) : (
          <>
            <h4 className="text-center mb-3">
              {isSignUp ? "Create Account" : "Sign In"}
            </h4>
            <Form>
              <Form.Group className="mb-3" controlId="formEmail">
                <Form.Label>Email</Form.Label>
                <Form.Control
                  type="email"
                  placeholder="Enter email"
                  onChange={(e) => setEmail(e.target.value)}
                />
              </Form.Group>

              <Form.Group className="mb-3" controlId="formPassword">
                <Form.Label>Password</Form.Label>
                <Form.Control
                  type="password"
                  placeholder="Enter password"
                  onChange={(e) => setPassword(e.target.value)}
                />
              </Form.Group>

              <Button
                variant="primary"
                className="w-100"
                onClick={handleSubmit}
              >
                {isSignUp ? "Sign Up" : "Sign In"}
              </Button>

              <div className="text-center mt-3">
                <Form.Text>
                  {isSignUp ? "Already have an account?" : "Don't have an account?"}{" "}
                  <Button
                    variant="link"
                    size="sm"
                    onClick={() => setIsSignUp(!isSignUp)}
                  >
                    {isSignUp ? "Sign In" : "Sign Up"}
                  </Button>
                </Form.Text>
              </div>
            </Form>
          </>
        )}
        {msg && <Alert variant="danger" className="mt-3 text-center">{msg}</Alert>}
      </Card>
    </Container>
  );
};
